






















































import { Component, Vue } from 'vue-property-decorator';
import Submit from '../components/Submit.vue';

const longTime = 60;

@Component({
  name: 'Home',
  components: {
    Submit,
  },
})
export default class Login extends Vue {
  phone = '';

  captcha = '';

  time = longTime;

  sendCaptchaStatus = false;

  validatorPhone(val: string) {
    return /^(?:(?:\+|00)86)?1\d{10}$/.test(val);
  }

  validatorCaptcha(val: string) {
    return /^\d{1,}$/.test(val);
  }

  onSubmit() {
    console.log('__LYG_JAX', this.phone, this.captcha); // debug-log

    // todo submit
    this.login();
  }

  async sendCaptcha() {
    if (!this.phone) {
      this.$toast('请填写手机号');
      return;
    }
    if (!this.validatorPhone(this.phone)) {
      this.$toast('请输入正确的手机号');
      return;
    }
    try {
      const { force } = this.$route.query;
      if (!force) {
        const data = await this.$xhr.api.get('/users/query_swfc_by_phone', {
          params: {
            phone: this.phone,
          },
        });
        if (data.code == 200) {
          if (!data.data.is_swfc) {
            this.$toast('您还不是森活汇会员');
            return;
          }
        } else {
          this.$toast(data.message || '网络不给力');
          return;
        }
      }
      try {
        await this.$xhr.api.post('/users/sendCaptcha', {
          params: {
            phone: this.phone,
          },
        });
        this.sendCaptchaStatus = true;
        this.$toast('验证码已发送到您的手机');
        const timer = setInterval(() => {
          this.time -= 1;
          if (this.time == 0) {
            clearInterval(timer);
            this.sendCaptchaStatus = false;
            this.time = longTime;
          }
        }, 1000);
      } catch (error) {
        this.$toast('验证码发送失败');
      }
    } catch (error) {
      this.sendCaptchaStatus = false;
      console.log('__LYG_JAX', error); // debug-log
      // eslint-disable-next-line
      // @ts-ignore
      this.$toast(error && error.message);
    }
  }

  async login() {
    try {
      const data = await this.$xhr.api.post('/users/login', {
        params: {
          phone: this.phone,
          captcha: this.captcha,
        },
      });
      if (data.switch) {
        this.$router.push({ name: 'home', query: { phone: this.phone } });
        return;
      }
      this.$router.push({ name: 'profile', query: { phone: this.phone } });
    } catch (error) {
      // eslint-disable-next-line
      // @ts-ignore
      this.$toast(error && error.message);
    }
  }

  onFormFailed(errorInfo: any) {
    const { errors } = errorInfo;
    const validFied = errors[0];
    this.$toast(validFied.message);
  }
}
